
.numbers {
    font-size: 25px;
    margin: 10px;
    display: grid;
    width: 472px;
    /* height: 20px; */
    grid-template-columns: repeat(9,1fr);
}

.num {
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 50px;
}


.top, .num {
    border-top: 3px solid black;
}
.left,.num {
    border-left: 3px solid black;
}
.right,.num9 {
    border-right: 3px solid black;
}
.bottom,.num {
    border-bottom: 3px solid black;
}
.test{
    position: absolute;
    top: 12%;
}