.box {
    margin: 10px;
    display: grid;
    grid-template-columns: auto auto auto;
    column-gap: 0px;
    row-gap: 0;
    
    /* height: 450px; */
    width: 450px;
    /* border: 1px solid black; */
    
}

.col{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 0px;
    border: 1px solid black;

}
.top, .col-1,.col-2,.col-3 {
    border-top: 3px solid black;
}
.left,.col-4,.col-7,.col-1 {
    border-left: 3px solid black;
}
.right,.col-3,.col-6,.col-9 {
    border-right: 3px solid black;
}
.bottom, .col-7,.col-8,.col-9 {
    border-bottom: 3px solid black;
}

.row {
    height: 50px;
    width: 50px;
    border: 1px solid black;
    font-size: 35px;
    font-weight: 600;
    /* display: flex;
    justify-content: center;
    align-items: center; */
    text-align: center;
}
.row div{
    text-align: center;

}